import React, { useEffect } from "react";
import NationalId from "../../assets/images/nationalid.png";
import passPort from "../../assets/images/passport.png";
import oldDriven from "../../assets/images/olddl.png";
import newDriven from "../../assets/images/newdl.png";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
export default function SelectDocument() {
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "bottom-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  let video = document.getElementById("#video");
  let canvas = document.querySelector("#canvas");

  async function startCamera() {
    let stream = await navigator.mediaDevices.getUserMedia({
      video: {
        deviceId: localStorage.getItem("cameraId"),
      },
      audio: false,
    });
    // stream.getVideoTracks()[0].label = "HP HD Camera (04f2:b51c)";
    video.srcObject = stream;
    video.play();
    setTimeout(() => {
      clickImage(stream);
    }, 100);
  }

  function clickImage(st) {
    const video = document.getElementById("video");
    let canvas = document.querySelector("#canvas");
    const context = canvas.getContext("2d");

    canvas.width = 150;
    canvas.height = 150;
    context.drawImage(video, 0, 0, 150, 90);
    const data = canvas.toDataURL("image/png");
    sessionStorage.setItem("picture", data);

    closeCamera(st);
  }

  function closeCamera(stream) {
    video.classList.add("d-none");
    canvas.classList.add("d-none");
    let tracks = stream.getTracks();
    tracks[0].stop();
  }
  useEffect(() => {
    video = document.querySelector("#video");
    canvas = document.querySelector("#canvas");
    startCamera();
    notify();
    toast.clearWaitingQueue();
  }, []);
  return (
    <div className="row justify-content-center">
      <video id="video" width="320" height="240" autoplay className=""></video>
      <canvas id="canvas" className="" width="320" height="240"></canvas>
      <ToastContainer limit={1} />
      <div className="col-md-8">
        <div className="row gap-3 justify-content-center">
          <div className="col-md-5 col-sm-5 rounded-5 shadow  overflow-hidden p-0">
            <Link
              to="/insertCard"
              onClick={(e) => {
                sessionStorage.setItem("doc", "National Id");
                sessionStorage.setItem("docNo", "1");
              }}
            >
              <img src={NationalId} alt="NationalId" className="w-100 imgDoc" />
              <div className="cardCapition p-3 text-center bg-white">
                <h3>{t("National Id")}</h3>
              </div>
            </Link>
          </div>
          <div className="col-md-5 col-sm-5 rounded-5 shadow  overflow-hidden p-0">
            <Link
              to="/insertCard"
              onClick={(e) => {
                sessionStorage.setItem("doc", "Passport");
                sessionStorage.setItem("docNo", "2");
              }}
            >
              <img src={passPort} alt="passPort" className="w-100 imgDoc" />
              <div className="cardCapition p-3 text-center bg-white">
                <h3>{t("Passport")}</h3>
              </div>
            </Link>
          </div>
          <div className="col-md-5 col-sm-5 rounded-5 shadow  overflow-hidden p-0">
            <Link
              to="/insertCard"
              onClick={(e) => {
                sessionStorage.setItem("doc", "Old Driving Licence");
                sessionStorage.setItem("docNo", "3");
              }}
            >
              <img src={oldDriven} alt="oldDriven" className="w-100 imgDoc" />
              <div className="cardCapition p-3 text-center bg-white">
                <h3>{t("Old Driving Licence")}</h3>
              </div>
            </Link>
          </div>
          <div className="col-md-5 col-sm-5 rounded-5 shadow  overflow-hidden p-0">
            <Link
              to="/insertCard"
              onClick={(e) => {
                sessionStorage.setItem("doc", "New Driving Licence");
                sessionStorage.setItem("docNo", "4");
              }}
            >
              <img src={newDriven} alt="newDriven" className="w-100 imgDoc" />
              <div className="cardCapition p-3 text-center bg-white">
                <h3>{t("New Driving Licence")}</h3>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
