import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Webcam from "react-webcam";

export default function TestCamera() {
  const [cameraDevices, setCameraDevices] = useState([]);
  const [selectedDeviceId, setSelectedDeviceId] = useState(null);
  const webcamRef = React.useRef(null);
  function Display() {
    document.getElementsByClassName("navbar")[0].classList.add("d-none");
    document.getElementById("root").classList.add("overflow-hidden");
  }
  function clear() {
    document.getElementsByClassName("navbar")[0].classList.remove("d-none");
    document.getElementById("root").classList.remove("overflow-hidden");
  }
  async function closeCamera() {
    let stream = await navigator.mediaDevices.getUserMedia({
      video: {
        deviceId:
          "a7cdaeae963732f288465dd3139680805fa98af75c2816b5401cc7a4587e51ee",
      },
      audio: false,
    });
    let tracks = stream.getTracks();
    tracks[0].stop();
  }
  useEffect(() => {
    Display();
    const getCameraDevices = async () => {
      try {
        const devices = await navigator.mediaDevices.enumerateDevices();
        const cameras = devices.filter(
          (device) => device.kind === "videoinput"
        );
        setCameraDevices(cameras);
      } catch (error) {
        console.error("Error accessing camera devices:", error);
      }
    };

    getCameraDevices();
    return () => {
      clear();
      closeCamera();
    };
  }, []);

  const handleDeviceChange = (event) => {
    setSelectedDeviceId(event.target.value);
    console.log(event.target.value);
  };

  const capture = React.useCallback(() => {
    const imageSrc = webcamRef.current.getScreenshot();
    console.log(imageSrc);
    console.log(selectedDeviceId);
  }, [webcamRef]);

  return (
    <div>
      <select onChange={handleDeviceChange}>
        <option value="">Select Camera</option>
        {cameraDevices.map((device) => (
          <option key={device.deviceId} value={device.deviceId}>
            {device.label}
          </option>
        ))}
      </select>

      {selectedDeviceId && (
        <div>
          <Webcam
            height={400}
            width={400}
            audio={false}
            ref={webcamRef}
            videoConstraints={{ deviceId: selectedDeviceId }}
          />

          <div className="btnSave w-100 d-flex justify-content-start">
            <Link
              to="/visitTime"
              className="btn btn-info"
              onClick={() => {
                localStorage.setItem("cameraId", selectedDeviceId);
              }}
            >
              Save
            </Link>
          </div>
        </div>
      )}
    </div>
  );
}
