import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import hotkeys from "hotkeys-js";
export default function ExitQrscreen(props) {
  let [checkval, setCheckVal] = useState("in");
  // console.log(props);
  useEffect(() => {
    let timer = setTimeout(() => {
      setCheckVal("out");
    }, 32000);
    return () => {
      clearTimeout(timer);
    };
  }, [props.children]);

  if (checkval === "in") {
    return props.children;
  } else {
    return <Navigate to="/visitTime" />;
  }
}
