import React, { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import $, { data } from "jquery";
import WOw from "wow.js";
import hrLogo from "../../assets/images/1436705.png";
import axios from "axios";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
export default function SelectDepratment() {
  let { id } = useParams();
  let [departmentList, setDepartment] = useState([]);
  let Navigate = useNavigate();

  useState(() => {
    new WOw().init();
    getDepar();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
  }, []);

  async function getDepar() {
    let data = await axios
      .get(`https://localhost:44301/api/Departments/GetAll`, {
        params: { companyId: `${id}` },
      })
      .catch((errorrr) => {
        Navigate("/selectTime");
      });
    if (data === undefined) {
      return;
    } else {
      setDepartment(data.data);
    }
  }
  function saveDepartment(id) {
    sessionStorage.setItem("department", `${id}`);
  }
  return (
    <>
      <div className="row w-100 h-100 g-3">
        <ToastContainer limit={1} />
        {departmentList.map((el) => {
          return (
            <div
              className="col-md-4 col-lg-3 col-sm-6  product position-relative"
              key={el.id}
            >
              <Link
                to={`/selectEmpolyees/${el.id}`}
                onClick={() => {
                  saveDepartment(el.id);
                }}
              >
                <div className="wow bounceInUp card  border-0 rounded-5 overflow-hidden shadow">
                  <div className="cardCapition bg-white p-4 text-center">
                    <h3>{el.name}</h3>
                  </div>
                </div>
              </Link>
            </div>
          );
        })}
      </div>
    </>
  );
}
