import React, { useEffect, useState } from "react";
import insertDocument from "../../assets/images/driving_old.gif";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import { data } from "jquery";
import { useSelector } from "react-redux";
export default function InserDocument() {
  let dataa = useSelector((data) => data.portSlice);
  const { t, i18n } = useTranslation();
  let id = sessionStorage.getItem("doc");
  let [OcrDataList, setOcrData] = useState("");

  let Navigate = useNavigate();

  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    getDataocr();
  }, []);

  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  async function getDataocr() {
    let data = await axios
      .get(`https://localhost:44301/api/OCR/ReadDocument`, {
        params: {
          DocumentType: sessionStorage.getItem("docNo"),
          companyId: sessionStorage.getItem("company"),
          terminalName: "Terminal1",
          registrationDate: dateObj.orginalDate,
        },
      })
      .then((res) => {
        if (window.location.hash === "#/insertCard") {
          if (res.data.isSuccess) {
            document.getElementById("textAlert").innerHTML = "";
            Navigate("/vistiorInformation");
            sessionStorage.setItem("ocr", `${JSON.stringify(res.data)}`);
          } else {
            // Navigate("/insertCard");
            document.getElementById("textAlert").innerHTML = res.data.message;
          }
        } else {
          return;
        }
      })
      .catch((errorrr) => {
        console.log(errorrr);
      });
  }
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  let dateObj = checkDate();
  return (
    <div className="row">
      <ToastContainer limit={1} />
      <h2 className="text-center">{t("EnterDoc")}</h2>
      <div className="col-md-12 m-auto d-flex align-items-center justify-content-center overflow-hidden">
        <img
          src={insertDocument}
          alt="documentInsertImage"
          width="600"
          height="400"
        />
      </div>
      <p className="text-danger" id="textAlert"></p>
    </div>
  );
}
