import React, { useState } from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/images/avms logo02-01.png";
import { useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import $ from "jquery";
export default function Navbar() {
  const { t, i18n } = useTranslation();

  function toggleFullScreen() {
    if (
      !document.fullscreenElement &&
      !document.mozFullScreenElement &&
      !document.webkitFullscreenElement &&
      !document.msFullscreenElement
    ) {
      // current working methods
      if (document.documentElement.requestFullscreen) {
        // document.documentElement.requestFullscreen();
        return;
      } else if (document.documentElement.msRequestFullscreen) {
        // document.documentElement.msRequestFullscreen();
        return;
      } else if (document.documentElement.mozRequestFullScreen) {
        // document.documentElement.mozRequestFullScreen();
        return;
      } else if (document.documentElement.webkitRequestFullscreen) {
        // document.documentElement.webkitRequestFullscreen(
        //   Element.ALLOW_KEYBOARD_INPUT
        // );
        return;
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      }
    }
  }

  return (
    <nav class="navbar bg-body-tertiary position-relative fixed-top w-100">
      <div class="container">
        <div className="d-flex align-items-center justify-content-start mx-3 w-50">
          <Link class="navbar-brand" href="/">
            <img src={logo} alt="AVMS" width="80" height="30" />
          </Link>
          {i18n.language == "ar" ? (
            <button
              onClick={() => {
                window.history.back();
              }}
              className="btn d-flex align-items-center justify-content-center"
              id="perv"
            >
              <i class="fa-solid fa-arrow-right ms-2 mainColor"></i>
              {t("back")}
            </button>
          ) : (
            <button
              onClick={() => {
                window.history.back();
              }}
              className="btn d-flex align-items-center justify-content-center"
              id="perv"
            >
              <i class="fa-solid fa-arrow-left me-2 mainColor"></i>
              {t("back")}
            </button>
          )}
        </div>
        <div class="select d-flex align-items-center gap-3">
          {i18n.language == "ar" ? (
            <button
              className="btn btn-outline-info"
              onClick={() => {
                i18n.changeLanguage("en");
                document
                  .getElementsByTagName("html")[0]
                  .setAttribute("dir", "ltr");
              }}
            >
              English
              <i class="fa-solid fa-earth-americas mx-2"></i>
            </button>
          ) : (
            <button
              className="btn btn-outline-info"
              onClick={() => {
                i18n.changeLanguage("ar");
                document
                  .getElementsByTagName("html")[0]
                  .setAttribute("dir", "rtl");
              }}
            >
              
              <i class="fa-solid fa-earth-americas mx-2"></i>
            </button>
          )}
          <button
            className="btn btn-outline-info"
            onClick={() => {
              toggleFullScreen();
            }}
          >
            {t("signOut")}
          </button>
        </div>
      </div>
    </nav>
  );
}
