import React, { useEffect, useState } from "react";
import qr from "../../assets/images/image-3702.png";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import greenMessage from "../../assets/images/think-before-you-print-logo-removebg-preview.png";
import ReactDOM from "react-dom";
import { QRCodeCanvas } from "qrcode.react";
import { ToastContainer, toast } from "react-toastify";
export default function QrScreen() {
  const { t, i18n } = useTranslation();
  let Navigate = useNavigate();
  let [loading, setLoading] = useState(false);
  let [OcrDataList, setOcrData] = useState([]);
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  let data = null;
  let qrImage = null;
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    function qrData() {
      let can = document.getElementById("qrCanvas");
      qrImage = can.toDataURL();
      qrImage = qrImage.toString().split(",").slice(1).join("");
      sessionStorage.setItem("qrImage", qrImage);
      data = getOcrData();
      setOcrData(data);
    }
    qrData();
  }, []);
  function getUserData() {
    let obj = {
      name: `${OcrDataList.firstName + " " + OcrDataList.lastName}`,
      documentID: OcrDataList.id,
      companyId: sessionStorage.getItem("company"),
      registerationDate: dateObj.orginalDate,
      qrBase64: sessionStorage.getItem("qrImage"),
    };

    return JSON.stringify(obj);
  }
  function getName() {
    return t("Aftef30s");
  }
  async function sendUserData() {
    setLoading(true);
    let userOBj = getUserData();
    console.log(userOBj);
    let dataa = await fetch("https://localhost:44301/api/print", {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: userOBj,
    });
    let daa = await dataa
      .json()

      .catch((errorrr) => {
        console.log(errorrr);
        setLoading(false);
      });
    setLoading(false);
    // if (dataa.ok) {
    //   Navigate("/qrScreen");
    //   sessionStorage.setItem("finalData", `${JSON.stringify(daa)}`);
    // } else {
    //   return;
    // }
  }
  function getOcrData() {
    return JSON.parse(sessionStorage.getItem("ocr"));
  }
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  let dateObj = checkDate();

  return (
    <div className="row h-100 w-100 align-items-center justify-content-center p-3">
      <ToastContainer limit={1} />
      <div className="col-lg-4 col-md-6 justify-content-center align-items-center">
        <div className="qrCard shadow p-4 rounded">
          <div className="d-flex gap-2">
            <h5>Name:</h5>
            <span>{`${
              OcrDataList.firstName + " " + OcrDataList.lastName
            }`}</span>
          </div>
          <div className="d-flex gap-2">
            <h5>Document Id:</h5>
            <span>{OcrDataList.id}</span>
          </div>
          <div className="d-flex gap-2">
            <h5>Company:</h5>
            <span>{sessionStorage.getItem("companyName")} </span>
          </div>

          {/* <div className="d-flex justify-content-center gap-2"> */}
          <div className="imgQr">
            <QRCodeCanvas
              value={JSON.parse(sessionStorage.getItem("ocr")).cardNumber}
              size={100}
              marginSize={0}
              id="qrCanvas"
              renderAs="canvas"
              className="p-0"
            />
          </div>
          {/* </div> */}
          <div className="date">
            <span>
              {dateObj.orginalDate.toString().split(" ").slice(0, 5).join(" ")}
            </span>
          </div>

          <Link
            to="/visitTime"
            onClick={() => {
              sendUserData();
            }}
            className="btn btn-info mt-4"
          >
            <span className="text-white">
              <i class="fa-solid fa-print"></i> Print
            </span>
          </Link>
        </div>
        <div className="col-md-12 m-auto">
          <img
            src={greenMessage}
            alt="greenMessage"
            loading="lazy"
            height="100"
            className="w-100"
          />
        </div>
      </div>
    </div>
  );
}
