import React, { useEffect, useState } from "react";
import $ from "jquery";
import WOw from "wow.js";
import Person from "../../assets/images/pic-1.png";
import Person2 from "../../assets/images/pic-2.png";
import Person3 from "../../assets/images/pic-3.png";
import { Link, useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
export default function SelectEmpolyees() {
  let { id } = useParams();
  let [employeeList, setEmployees] = useState([]);
  let Navigate = useNavigate();

  useState(() => {
    toast.clearWaitingQueue();
    new WOw().init();
    getEmpolyee();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  async function getEmpolyee() {
    let data = await axios
      .get(`https://localhost:44301/api/Employees/GetAll`, {
        params: { departmentId: `${id}` },
      })
      .catch((errorrr) => {
        Navigate("/selectTime");
      });
    console.log(data);
    if (data === undefined) {
      return;
    } else {
      notify();
      setEmployees(data.data);
    }
  }
  function saveEmpolyee(id) {
    sessionStorage.setItem("empolyee", `${id}`);
  }
  return (
    <div className="row w-100 h-100 g-3">
      <ToastContainer limit={1} />
      {employeeList.map((el) => {
        return (
          <div
            className="col-md-4 col-lg-3 col-sm-6  product position-relative"
            key={el.id}
          >
            <Link
              to="/selectTime"
              onClick={() => {
                saveEmpolyee(el.id);
              }}
            >
              <div class="wow bounceInUp card p-2 rounded-5 shadow border-0 py-4">
                <div className="row gap-3">
                  <div className="col-md-12">
                    <div class="carddd__avatar m-auto overflow-hidden rounded-circle">
                      <img
                        src={"data:image/jpeg;base64," + el.photo}
                        alt="Avatar img"
                        className="w-100"
                      />
                    </div>
                  </div>
                  <div className="col-md-12 card-text">
                    <h2 class="text-center text-dark">{el.name}</h2>
                    {/* <div class="text-muted text-center">Frontend</div> */}
                  </div>
                </div>
              </div>
            </Link>
          </div>
        );
      })}
    </div>
  );
}
