import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

export default function SelectTime() {
  const { t, i18n } = useTranslation();

  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
  }, []);
  function setDate() {
    var today = new Date();
    var dd = today.getDate() + 1;
    var mm = today.getMonth() + 1;
    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = "0" + dd;
    }
    if (mm < 10) {
      mm = "0" + mm;
    }
    today = yyyy + "-" + mm + "-" + dd;
    document.getElementById("datefield").setAttribute("min", today);
  }
  function saveData() {
    let date = document.getElementById("datefield").value;
    let time = document.getElementById("time").value;
    let dateObj = {
      date: date,
      time: time,
    };
    sessionStorage.setItem("date", JSON.stringify(dateObj));
  }

  return (
    <div className="row">
      <ToastContainer limit={1} />
      <div className="col-md-8 w-100 h-100 d-flex align-items-center justify-content-center">
        <div className="row g-3">
          <h1>{t("selectDate")}</h1>
          <div className="col-md-12 mb-3">
            <div className="form-control">
              <label htmlFor="datefield" className="mb-3">
                {t("date")}
              </label>
              <input
                type="date"
                id="datefield"
                className="form-control"
                onClick={setDate}
              />
            </div>
          </div>
          <div className="col-md-12">
            <div className="form-control">
              <label htmlFor="time" className="mb-3">
                {t("time")}
              </label>
              <input type="time" id="time" className="form-control" />
            </div>
          </div>
          <div className="col-md-3">
            <Link
              to={"/selectDoc"}
              className="btn btn-info w-100"
              onClick={saveData}
            >
              {t("save")}
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
