import React, { useEffect, useState } from "react";
import $ from "jquery";
import WOw from "wow.js";
import logo from "../../assets/images/png-transparent-puma-logo-adidas-tamas-eyecare-adidas-cat-like-mammal-text-carnivoran-removebg-preview.png";
import axios from "axios";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
export default function Login() {
  let [ProductList, setProducts] = useState([]);
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    new WOw().init();
    getComapny();
  }, []);
  const { t, i18n } = useTranslation();
  function getName() {
    return t("Aftef30s");
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  async function getComapny() {
    let { data } = await axios.get(
      `https://localhost:44301/api/Companies/GetAll`
    );
    setProducts(data);
  }
  function saveCompany(id, name) {
    sessionStorage.setItem("company", `${id}`);
    sessionStorage.setItem("companyName", `${name}`);
  }
  return (
    <div className="row w-100 h-100 g-3">
      <ToastContainer limit={1} />
      {ProductList.map((product) => {
        return (
          <>
            <div
              key={product.id}
              className="col-md-3 col-lg-2 col-sm-6  product position-relative"
            >
              <Link
                to={"/selectDepartment/" + product.id}
                onClick={() => {
                  saveCompany(product.id, product.name);
                }}
              >
                <div className="wow bounceInUp card border-0 rounded-5 overflow-hidden shadow">
                  <img
                    src={"data:image/jpeg;base64," + product.logo}
                    // src={logo}
                    className="w-100"
                    height="150px"
                    alt=""
                  />
                  <div className="cardCapition p-3 text-center">
                    <h5>{product.name}</h5>
                  </div>
                </div>
              </Link>
            </div>
          </>
        );
      })}
    </div>
  );
}
