import React from "react";
import { useSelector } from "react-redux";
import { Navigate } from "react-router-dom";

export default function CheckWelcomeVisit(props) {
  let data = useSelector((data) => data.welcomeCheck);

  if (data.count > 0) {
    return <Navigate to="/VisitTime" />;
  } else {
    return props.children;
  }
}
