import { createSlice } from "@reduxjs/toolkit";

let settingSlice = createSlice({
  name: "welcomeVisit",
  initialState: { count: 0 },
  reducers: {
    changeSettings: (data, action) => {
      data.count += 1;
    },
  },
});
export let welcomeVisit = settingSlice.reducer;
export let { changeSettings } = settingSlice.actions;
