import React, { useEffect, useState } from "react";
import document from "../../assets/images/images.jpg";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { ToastContainer, toast } from "react-toastify";
import axios from "axios";
export default function VistiorInformation() {
  const { t, i18n } = useTranslation();
  let Navigate = useNavigate();
  let [loading, setLoading] = useState(false);
  let [OcrDataList, setOcrData] = useState([]);

  let data = null;
  useEffect(() => {
    notify();
    toast.clearWaitingQueue();
    data = getOcrData();
    setOcrData(data);
  }, []);
  function getUserData() {
    let obj = {
      name: `${OcrDataList.firstName}`,
      companyId: sessionStorage.getItem("company"),
      deparmentId: sessionStorage.getItem("department"),
      employeeId: sessionStorage.getItem("empolyee"),
      deviceId: "1",
      documentIdname: `${OcrDataList.lastName}`,
      documentCameraShot: sessionStorage
        .getItem("picture")
        .split(",")
        .slice(1)
        .join(),
      documentImageId: OcrDataList.documentImage,
      documentIdenglishName: OcrDataList.enalishName,
      documentIdno: OcrDataList.id,
      documentIdaddress: OcrDataList.address,
      registrationDate: dateObj.orginalDate,
      registrationTime: dateObj.time,
      description: null,
      notes: null,
      Qr: `${JSON.parse(sessionStorage.getItem("ocr")).cardNumber}`,
      documentFirstName: `${OcrDataList.firstName}`,
      documentNumber: null,
    };
    return JSON.stringify(obj);
  }
  function getName() {
    return t("Aftef30s");
  }
  async function sendUserData() {
    setLoading(true);
    let userOBj = getUserData();
    let dataa = await fetch("https://localhost:44301/api/Registrations", {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: userOBj,
    });
    let daa = await dataa
      .json()

      .catch((errorrr) => {
        console.log(errorrr);
        setLoading(false);
      });
    setLoading(false);
    if (dataa.ok) {
      Navigate("/qrScreen");
      sessionStorage.setItem("finalData", `${JSON.stringify(daa)}`);
    } else {
      return;
    }
  }
  const notify = () =>
    toast.warn(getName(), {
      position: "top-center",
      autoClose: 30000,
      hideProgressBar: false,
      closeOnClick: false,
      pauseOnHover: false,
      draggable: false,
      progress: undefined,
      theme: "colored",
      closeButton: false,
    });

  function getOcrData() {
    return JSON.parse(sessionStorage.getItem("ocr"));
  }
  function checkDate() {
    if (sessionStorage.getItem("status") === "schedule") {
      let scheduleDAte = JSON.parse(sessionStorage.getItem("date"));
      let date = new Date(scheduleDAte.date + " " + scheduleDAte.time);
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    } else {
      let date = new Date();
      let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
      let Time = date.toString().split(" ").slice(4, 5).join(" ");
      let monthFormat = date.toISOString();
      const offsetMinutes = date.getTimezoneOffset();
      date.setMinutes(date.getMinutes() - offsetMinutes);
      const formattedDate = date.toISOString().substring(0, 23);
      return {
        fullDate: monthFormat,
        orginalDate: formattedDate,
        date: dateMonth,
        time: Time,
      };
    }
  }
  let dateObj = checkDate();
  return (
    <div className="row visitorInfor">
      <ToastContainer limit={1} />
      <div className="col-md-10 m-auto">
        <div className="row gap-4 justify-content-center">
          <div className="col-md-7">
            <h2 className="mb-5">{t("VisitorInfor")}</h2>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-user mx-2"></i>
              <span>{t("fullName")}</span>
            </h5>
            <span>
              {`${OcrDataList.firstName}` + " " + `${OcrDataList.lastName}`}
            </span>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-map-location-dot mx-2"></i>
              <span>{t("address")}</span>
            </h5>
            <span>{OcrDataList.address}</span>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-id-card mx-2"></i>
              <span>{t("documentType")}</span>
            </h5>
            <span>{sessionStorage.getItem("doc")}</span>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-xmarks-lines mx-2"></i>
              <span>{t("documentNumber")}</span>
            </h5>
            <span>{OcrDataList.id}</span>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-calendar-days mx-2"></i>
              <span>{t("date")}</span>
            </h5>
            <span>{dateObj.date}</span>
          </div>
          <div className="col-md-7 d-flex justify-content-between">
            <h5>
              <i class="fa-solid fa-clock mx-2"></i>
              <span>{t("time")}</span>
            </h5>
            <span>{dateObj.time}</span>
          </div>
          <div className="col-md-7">
            <div className="row justify-content-between">
              <div className="col-md-4">
                <Link to="/UpdateOcrData" className="btn btn-danger w-100 mb-2">
                  {t("edit")}
                </Link>
              </div>
              <div className="col-md-4">
                {loading ? (
                  <button type="button" className="btn btn-success">
                    <i className="fa-solid fa-spinner fa-spin text-white"></i>
                  </button>
                ) : (
                  <button
                    // to="/qrScreen"
                    className="btn btn-info w-100"
                    onClick={() => {
                      sendUserData();
                    }}
                  >
                    {t("save")}
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
