import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import $, { event } from "jquery";
import hotkeys from "hotkeys-js";
import WOw from "wow.js";
import time from "../../assets/images/time_5246266.png";
import calender from "../../assets/images/calendar_2693507.png";
import { useTranslation } from "react-i18next";
export default function VisitTime() {
  const { t, i18n } = useTranslation();

  function setTodayData() {
    let date = new Date();
    let dateMonth = date.toString().split(" ").slice(0, 4).join(" ");
    let Time = date.toString().split(" ").slice(4, 5).join(" ");
    let dataToday = {
      date: dateMonth,
      time: Time,
    };
    sessionStorage.setItem("today", JSON.stringify(dataToday));
  }
  useState(() => {
    sessionStorage.clear();
    new WOw().init();
  }, []);
  return (
    <div className="row h-100 w-100 g-3">
      <div className="col-md-12 d-flex align-items-center justify-content-center flex-column">
        <p>{t("pickOne")}</p>
      </div>
      <div className="col-md-6 d-flex align-items-center justify-content-center">
        <Link
          onClick={setTodayData}
          to="/companies"
          className="wow bounceInLeft cardLang card rounded shadow p-5 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
        >
          <div className="langImg p-2">
            <img src={time} alt="time" loading="lazy" />
          </div>
          <input type="hidden" id="inputHidden" />
          <h3>{t("Today")}</h3>
        </Link>
      </div>
      <div className="col-md-6 d-flex align-items-center justify-content-center">
        <Link
          data-time="schedule"
          to="/companies"
          className="wow bounceInRight cardLang card rounded shadow p-5 d-flex align-items-center justify-content-center flex-column gap-2 border-0 rounded-5"
          onClick={(e) => {
            sessionStorage.setItem("status", "schedule");
          }}
        >
          <div className="langImg p-3">
            <img src={calender} alt="calender" loading="lazy" />
          </div>
          {/* <input id="datefield" type="date" min="2023-07-24"></input> */}
          <h3>{t("schedule")}</h3>
        </Link>
      </div>
    </div>
  );
}
